import { findAllByDisplayValue } from "@testing-library/react";

export const initialState = {
    user: null,
    playlists: [],
    spotify: null,
    discover_weekly: null,
    top_artists: null,
    playing: false,
    item: null,
    token: 'BQCW2odNwInANLvzu2sSGx_rr13K6YRj5gORav54r9EZhj5z8I…qZKXP1BjpFa8wQ7LJ8bLdlJzzuCDM354A5IYefaLdBt9C-fot',
};

const reducer = (state, action) => {
    console.log(action);

    switch(action.type) {
        case 'SET_USER':
            return {
                ...state,
                user: action.user
            };
        
        case "SET_PLAYING":
            return {
                ...state,
                playing: action.playing,
            }; 
            
            case "SET_ITEM":
                return {
                  ...state,
                  item: action.item,
                };
                
                case "SET_TOP_ARTISTS":
                    return {
                      ...state,
                      top_artists: action.top_artists,
                    };
                    
                    case "SET_SPOTIFY":
                        return {
                          ...state,
                          spotify: action.spotify,
                        };
                       

        case 'SET_TOKEN':
            return {
                ...state,
                user: action.user,
            };

        case 'SET_PLAYLISTS':
            return{
                ...state,
                playlists: action.playlists,
            }; 

        case "SET_DISCOVER_WEEKLY":
            return {
                ...state,
                discover_weekly: action.discover_weekly,
            };   
            
        default:
          return state;        
    }
}

export default reducer;